.projects__row:first-child .project__wrap { padding-bottom: 9%; }

@media only screen and (max-width: 767px) { .projects__row:first-child .project__wrap { padding-bottom: 0; } }

.projects__row:last-child .project__wrap { padding-top: 5%; }

@media only screen and (max-width: 767px) { .projects__row:last-child .project__wrap { padding-bottom: 0; } }

.projects__row:nth-child(odd) .project__wrap { padding-bottom: 5%; }

@media only screen and (max-width: 767px) { .projects__row:nth-child(odd) .project__wrap { padding-bottom: 0; } }

.projects__row:nth-child(even) .project__wrap { padding-top: 5%; }

@media only screen and (max-width: 767px) { .projects__row:nth-child(even) .project__wrap { padding-bottom: 0; } }

.project__name { position: relative; padding-left: 21px; font-family: "Arbutus Slab", Helvetica, Arial, sans-serif; font-size: 30px; color: #3F566E; letter-spacing: 0; line-height: 1.2; transition: color .3s; vertical-align: top; display: inline-block; }

@media only screen and (max-width: 1023px) { .project__name { font-size: 24px; } }

@media only screen and (max-width: 767px) { .project__name { font-size: 18px; } }

.project__name:hover { color: #FFDC45; }

.project__name .icon-arrow-right { margin-left: 8px; font-size: 9px; vertical-align: middle; }

.project__line { position: absolute; top: 0; left: 0; bottom: 0; width: 4px; background-color: #FFDC45; }

.project__line_pink { background-color: #FF539E; }

.create { padding: 150px 0 162px; color: #303A44; background-color: #FFDC45; }

@media only screen and (max-width: 1023px) { .create { padding: 120px 0; } }

@media only screen and (max-width: 767px) { .create { margin-top: 40px; padding: 60px 0; } }

.create__wrap { max-width: 426px; }

.create__title-lg { margin-bottom: 17px; color: #fff; }

.create__footer { margin-top: 23px; }

.create p { font-size: 16px; font-weight: 400; line-height: 21px; }

.footer { padding: 54px 0; color: #fff; background-image: linear-gradient(229deg, #36424D 3%, #2B333C 100%); border-bottom: 5px solid #000; }

.footer__wrap { width: 100%; display: table; }

@media only screen and (max-width: 767px) { .footer__wrap { display: block; } }

.footer__contacts, .footer__button { vertical-align: middle; display: table-cell; }

@media only screen and (max-width: 767px) { .footer__contacts, .footer__button { display: block; } }

.footer__button { text-align: right; }

@media only screen and (max-width: 767px) { .footer__button { margin-top: 20px; text-align: center; } }

.footer__info { vertical-align: top; display: inline-block; }

@media only screen and (max-width: 767px) { .footer__info { display: block; padding-bottom: 22px; } }

.footer__info:not(:last-child) { margin-right: 50px; }

@media only screen and (max-width: 767px) { .footer__info:not(:last-child) { margin: 0; } }

.footer__icon { margin-bottom: 7px; }

.footer__subtitle { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-size: 16px; font-weight: 700; line-height: 1.4; letter-spacing: 0.46px; }

.footer__meta { font-size: 15px; font-weight: 400; line-height: 20px; letter-spacing: -0.02px; }

.footer__meta a { color: inherit; transition: color 0.25s; }

.no-touch .footer__meta a:hover { color: #FFDC45; }

.footer__subnav { margin: 69px -13px 0; }

@media only screen and (max-width: 1023px) { .footer__subnav { margin-top: 13px; } }
